
import Scroll from '../../../base/scroll/scroll'
import Checkbtn from '../../../base/checkbtn/checkbtn'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV2Data from 'vux/src/datas/china_address_v2.json'
import value2name from 'vux/src/filters/value2name.js'

import { mapActions, mapGetters } from 'vuex'

export default {
name: 'userAddress',
components: {
Scroll,
XInput,
Group,
Checkbtn,
XAddress
},
data () {
return {
infos: [
'请填写淘宝常用收货地址，中奖下单必须与改地址一致！平台会定期检查，如发现不一致，将封号处理！',
'该地址将作为后期活动寄送的收货地址'
],
isShowAdd: false,
addressOne: [],
addressOneName: '',
addressTwo: '',
addressName: '',
addressPhone: '',
isChecked: true,
btnSaveState: false,
addressData: ChinaAddressV2Data,
isShowAddress: false,
title: '默认'
}
},
computed: {
addressItem: {
get () {
return {
name: this.userInfo.username,
isDefault: true,
phone: this.userInfo.telephone,
addressInfo: this.userInfo.postAddress
}
},
set (val) {
return val
}
},
...mapGetters([
'userInfo'
])
},
watch: {
addressOne (newval) {
this.addressOneName = this.getName(newval)
this.regExpClicked()
},
addressTwo () {
this.regExpClicked()
},
addressName () {
this.regExpClicked()
},
addressPhone () {
this.regExpClicked()
}
},
methods: {
showAddAddress () {
this.isShowAdd = true
},

chooseAddress () {
this.isShowAddress = true
},

getName (value) {
return value2name(value, ChinaAddressV2Data)
},

regExpClicked () {

let hasCore = (this.addressOneName.length > 1)
let hasAdd = this.$refs.validAdd.valid
let hasName = this.$refs.validName.valid
let hasPhone = this.$refs.validPhone.valid
if (hasCore && hasAdd && hasName && hasPhone) {
this.btnSaveState = true
return false
}
this.btnSaveState = false
},

saveAddress () {

if (this.btnSaveState) {
console.log('保存')
this.$axios.post('/api/user/update', {
telephone: this.userInfo.telephone,
postAddress: this.addressOneName,
phone: this.addressPhone,
name: this.addressName
}).then((response) => {

let that = this
let obj = this.$store.state.userInfo
obj.addressInfo = this.addressOneName
obj.phone = this.addressPhone
obj.name = this.addressName
this.setUserInfo(obj)
this.$vux.toast.show({
text: '保存成功',
onHide () {
that.$router.push({ name: 'settings' })
}
})
}).catch((error) => {
console.log(error)
})
}
},

updateAddress () {
this.isShowAdd = true
},
...mapActions([
'setUserInfo'
])
}
}



